.experience__header {
  color: $color-periwinkle-gray;
  
  @extend %header;
}

.experience__title {
  display: block;
  background-color: $color-gumbo;
  padding: 2rem;
  font-size: 1.8rem;
  color: $color-white;
  cursor: pointer;
}

.my-cv__icon--plus {
  float: right;
}

.experience__input,
.experience__details {
  display: none;
}

.experience__input:checked + .experience__title + .experience__details {
  display: block;
}

.experience__input:checked + .experience__title {
  .experience__close {
    pointer-events: all;

    .my-cv__icon--plus {
      transform: rotateZ(45deg);
      cursor: pointer;
    }
  }
}

.experience__close {
  pointer-events: none;
}

.experience__date {
  font-style: italic;
}

.experience__details {
  border: 0.2rem solid $color-gumbo;
  margin-top: -0.1rem;
  border-top: 0;
}

.experience__company {
  font-weight: bold;
}

.experience__company,
.experience__date {
  padding: 1rem;
  font-size: 1.4rem;
}
.experience__top {
  display: flex;
  justify-content: space-between
}

.experience__responsability {
  padding: 0.5rem 1rem;
  line-height: 1.5;

  &:nth-child(odd) {
    background-color: $color-fantasy;
  }

  &:nth-child(even) {
    background-color: $color-twilight;
  }
}